<template>
  <div class="medical_insurance">
    <p class="medical_insurance-title">แผนความคุ้มครองค่ารักษาพยาบาลที่คุณต้องการ</p>
    <el-tabs v-model="activeName" @tab-click="handleClick" class="medical_tab">
      <el-tab-pane label="กรณีผู้ป่วยใน - IPD" name="กรณีผู้ป่วยใน - IPD" class="baseMain">
        <p>
          <span>1. โรงพยาบาลที่คุณใช้บริการเป็นประจำ</span>
          <aia-select v-model="selectHospital" placeholder="请选择" @change="hospitalChange" style="width:50%;">
            <aia-option
              v-for="item in hospitalList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></aia-option>
          </aia-select>
        </p>
        <p>
          <span>2. หากคุณต้องเขารับการรักษาพยาบาล คุณจะเลือกเข้าพักรักษาตัวในห้องพักฟื้นรูปแบบใด</span>
          <aia-radio-group v-model="roomType" @change="changeRoomType">
            <aia-radio-button label="ห้องเดี่ยว"></aia-radio-button>
            <aia-radio-button label="ห้องพิเศษ"></aia-radio-button>
            <aia-radio-button label="ห้อง VIP"></aia-radio-button>
          </aia-radio-group>
        </p>
        <p>
          <span>3. สรุปความคุ้มครองที่คุณควรมี</span>
          <aia-input type="text" disabled :value="recommendedPlan"></aia-input>
        </p>
      </el-tab-pane>
      <el-tab-pane label="กรณีผู้ป่วยนอก - OPD" name="กรณีผู้ป่วยนอก - OPD" class="baseMain">
        <p>
          <span>1. ค่าใช้จ่ายเฉลี่ยต่อครั้ง</span>
          <aia-input type="number" v-model="OutpatientExistingExpenseTime"></aia-input>
        </p>
        <p>
          <span>2. คุณเตรียมไว้แล้วเท่าไร? / ต่อครั้ง</span>
          <aia-input type="number" v-model="OutpatientPreparationPertime"></aia-input>
        </p>
        <p>
          <span>3. สรุปความคุ้มครองที่คุณควรมี</span>
          <aia-input disabled :value="OPDrecomPlan"></aia-input>
        </p>
      </el-tab-pane>
      <el-tab-pane label="ค่าชดเชยรายวัน" name="ค่าชดเชยรายวัน" class="baseMain">
        <p>
          <span>1. คุณเตรียมไว้แล้วเท่าไร</span>
          <aia-input type="number" v-model="allPreparation"></aia-input>
        </p>
        <p>
          <span>2. กรอกรายได้ต่อเดือนเพื่อให้ระบบช่วยคำนวณ</span>
          <aia-input type="number" v-model="incomePerMonth"></aia-input>
        </p>
        <p>
          <span>3. สรุปความคุ้มครองที่คุณควรมี</span>
          <span class="directionText">ความคุ้มครองแนะนำ = 100% ของรายได้ต่อวัน</span>
          <span
            class="directionText"
          >เช่น เงินเดือนเดือนละ 30,000 บาท ควรซื้อค่าชดเชยวันละ 1,000 บาท</span>
          <aia-input disabled :value="dailyrecomPlan"></aia-input>
        </p>
      </el-tab-pane>
    </el-tabs>
    <p class="tag-title">
      <label>คลิกที่นี่</label>เพื่อดูตัวอย่างค่าใช้จ่ายโรคร้ายแรง
    </p>
  </div>
</template>
<script>
import HospitalList from '../../utils/hospitalList'
export default {
  name: "medicalInsurance",
  data() {
    return {
      activeName: "กรณีผู้ป่วยใน - IPD",
      selectHospital: "",
      optionData2: "",
      roomType: "ห้องเดี่ยว",
      hospitalList: HospitalList,
      hospitalVipRoom:'',
      hospitalSpecialRoom:'',
      hospitalSingleRoom:'',
      recommendedPlan:'',
      //OPD Outpatient-OPD
      OutpatientExistingExpenseTime:'',
      OutpatientPreparationPertime:'',
      //Daily Compensation
      allPreparation:'',
      incomePerMonth:''
    };
  },
  created(){
    console.log(HospitalList.length)
  },
  methods: {
    handleClick(tab, event) {},
    hospitalChange(val){
      this.hospitalList.forEach((item,index) => {
        if(item.value == val){
          this.hospitalVipRoom = item.vipRoom;
          this.hospitalSpecialRoom = item.specialRoom;
          this.hospitalSingleRoom = item.singleRoom;
          this.changeRoomType();
        }
      })
    },
    changeRoomType(val){
      this.roomType == val;
      if(this.roomType == 'ห้องเดี่ยว'){
        this.recommendedPlan = this.hospitalSingleRoom;
      }else if(this.roomType == 'ห้องพิเศษ'){
        this.recommendedPlan = this.hospitalSpecialRoom;
      }else if(this.roomType == 'ห้อง VIP'){
        this.recommendedPlan = this.hospitalVipRoom;
      }
    }
  },
  computed:{
    OPDrecomPlan(){
      let finalPlan = this.OutpatientExistingExpenseTime - this.OutpatientPreparationPertime;
      if(finalPlan < 0){
        finalPlan = 0;
      }
      return finalPlan;
    },
    dailyrecomPlan(){
      let dailyfinalPlan = this.allPreparation - this.incomePerMonth;
      if(dailyfinalPlan < 0){
        dailyfinalPlan = 0;
      }
      return dailyfinalPlan;
    }
  }
};
</script>
